import { Component, OnInit } from '@angular/core';
import { AjaxService } from '../../services/ajax/ajax.service';

@Component({
  selector: 'app-highlighted-events',
  templateUrl: './highlighted-events.component.html',
  styleUrls: ['./highlighted-events.component.scss']
})
export class HighlightedEventsComponent implements OnInit {
  hEventsObj;
  hEventsArr = [];

  constructor(private _ajax: AjaxService) { }

  ngOnInit() {
    this.loadhighlightedEventsData();
  }

  async loadhighlightedEventsData () {
    this.hEventsObj = await this._ajax.get(window['urls'].highlightedEventsData);
    this.hEventsArr = this.hEventsObj.hEvents;  
  }
}
