import { Component, OnInit } from '@angular/core';
import { AjaxService } from '../../services/ajax/ajax.service';

@Component({
  selector: 'app-home-slider',
  templateUrl: './home-slider.component.html',
  styleUrls: ['./home-slider.component.scss']
})
export class HomeSliderComponent implements OnInit {
  sliderData;
  sliderArr = [];

  constructor(private _ajax: AjaxService) { }


  ngOnInit() {
    this.loadSliderData();
  }

  async loadSliderData () {
    this.sliderData = await this._ajax.get(window['urls'].homeSliderData);
    this.sliderArr = this.sliderData.slides;
    setTimeout(() => {
      this.initHomeSlider();
    }, 1000);    
  }

  initHomeSlider () {
    //console.log("inside initHomeSlider");
    //document.addEventListener('DOMContentLoaded', function() {
      // console.log("inside initHomeSlider inside");
      let M: any = window["M"];
      var elems = document.querySelectorAll('.slider');
      var instances = M.Slider.init(elems, {});
    //});
  }

}
