import { Component, OnInit } from '@angular/core';
import { AjaxService } from '../../services/ajax/ajax.service';

@Component({
  selector: 'app-members',
  templateUrl: './members.component.html',
  styleUrls: ['./members.component.scss']
})
export class MembersComponent implements OnInit {
  membersObj;
  membersArr = [];

  constructor(private _ajax: AjaxService) { }

  ngOnInit() {
    this.loafMembersData();
  }

  async loafMembersData () {
    this.membersObj = await this._ajax.get(window['urls'].committeeMembersData);
    this.membersArr = this.membersObj.members; 
    console.log(this.membersArr); 
  }

}
